import React, {useState} from 'react'
import { useStaticQuery, graphql, Link  } from 'gatsby'
import BreadcrumContent from '../../../../../Global/Pages/BreadcrumbContent.component'
import Pagination from '../../../../../Global/Pages/Pagination.component'
import AdvanceSearchWidgetSidebar from '../../../../../Global/App/SidebarWidgets/AdvancedSearchWidget.component'
import { SearchResult } from '../../../../../Global/Pages/Listing/Grid/GridSearchResult.component'

const V3Section=()=>{
    const [SideBarShow, setSideBarShow] = useState(false);
    const handleSidebar=()=>{
        setSideBarShow(!SideBarShow)
            document.querySelector('body').classList.toggle('body_overlay')
            document.querySelector('.sidebar_content_details').classList.toggle('sidebar_ml0')
    }
    const data = useStaticQuery(graphql`
        query{
             fp1: file(relativePath: {eq: "property/fp1.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp2: file(relativePath: {eq: "property/fp2.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp3: file(relativePath: {eq: "property/fp3.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp4: file(relativePath: {eq: "property/fp4.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
            }
            fp15: file(relativePath: {eq: "property/fp15.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			fp16: file(relativePath: {eq: "property/fp16.jpg"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			author: file(relativePath: {eq: "property/pposter1.png"}) {
                id
                childImageSharp {
                fluid {
                    originalImg
                }
                }
			}
			
        }`)
    const PropertyItem=({children,price,featured,category,title,listfor})=>{
        return(
            
            <div className="feat_property home7 style4">
            <Link to="/single/v3">
            <div className="thumb">
                {children}
                {featured ?
                <div className="thmb_cntnt style2">
                    <ul className="tag mb0">
                        <li className="list-inline-item"><span >{listfor}</span></li>
                        <li className="list-inline-item"><span >Featured</span></li> 
                    </ul>
                </div>
                :
                <div className="thmb_cntnt style2">
                    <ul className="tag mb0">
                        <li className="list-inline-item"><span >{listfor}</span></li>
                        <li className="list-inline-item dn"><span ></span></li> 
                    </ul>
                </div>
                }
                <div className="thmb_cntnt style3">
                    <ul className="icon mb0">
                        <li className="list-inline-item"><span><span className="flaticon-transfer-1"></span></span></li>
                        <li className="list-inline-item"><span><span className="flaticon-heart"></span></span></li>
                    </ul>
                    <span className="fp_price">${price}<small>/mo</small></span>
                </div>
            </div>
            </Link>
            <div className="details">
            <Link to="/single/v3">
                <div className="tc_content">
                    <p className="text-thm">{category}</p>
                    <h4>{title}</h4>
                    <p><span className="flaticon-placeholder"></span> 1421 San Pedro St, Los Angeles, CA 90015</p>
                    <ul className="prop_details mb0">
                        <li className="list-inline-item"><span className="text-thm3">Beds: 4</span></li>
                        <li className="list-inline-item"><span className="text-thm3">Baths: 2</span></li>
                        <li className="list-inline-item"><span className="text-thm3">Sq Ft: 5280</span></li>
                    </ul>
                </div>
                </Link>
                <div className="fp_footer">
                    <ul className="fp_meta float-left mb0">
                        <li className="list-inline-item"><Link to="/agent/single">
                        <img src={data.author.childImageSharp.fluid.originalImg} alt="pposter1.png"/>
                        </Link></li>
                        <li className="list-inline-item"><Link to="/agent/single">Ali Tufan</Link></li>
                    </ul>
                    <div className="fp_pdate float-right">4 years ago</div>
                </div>
            </div>
        </div>
        
        )
            
        }
    return(
        <section className="our-listing bgc-f7 pb30-991">
			<div className="container">
                <div className="row">
                    <div className="col-lg-12">
                        <div className="listing_sidebar dn db-991">
                            <div className="sidebar_content_details style3">
                                <div className="sidebar_listing_list style2 mobile_sytle_sidebar mb0">
                                    <div className="sidebar_advanced_search_widget">
                                        <h4 className="mb25">
                                        Advanced Search <Link className="filter_closed_btn float-right" to="#" onClick={handleSidebar}><small>Hide Filter</small> <span className="flaticon-close"></span>
                                        </Link>
                                        </h4>
                                        <AdvanceSearchWidgetSidebar index="2" style2/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="row">
                     <div className="col-lg-6">
                        <div className="dn db-991">
						<div id="main2" >
							<span id="open2" className="flaticon-filter-results-button filter_open_btn style3" onClick={handleSidebar}> Show Filter</span>
						</div>
                    </div>
                    </div>
                    </div>
                    <div className="col-lg-6">
                    <BreadcrumContent title="Simple Listing – Grid View" active="Simple Listing – Grid View 3"/>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-4 col-xl-4">
                    <div className="sidebar_listing_grid1 dn-991">
						<div className="sidebar_listing_list">
							<div className="sidebar_advanced_search_widget">
                                <AdvanceSearchWidgetSidebar index="1"/>
                            </div>
                        </div>
                    </div>
                    </div>
                    <div className="col-md-12 col-lg-8">
                        <div className="row">
                         <SearchResult/>
                        </div>
                        <div className="row">
                            <div className="col-md-6 col-lg-6">
                                <PropertyItem listfor='For Rent'
                                featured
                                price="13000"
                                category="Appartment"
                                title="Renovated Apartment"
                                >
                                    <img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                </PropertyItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                            <PropertyItem listfor='For Rent'
                                price="13000"category="Villa"
                                title="Gorgeous Villa Bay View"
                                >
                                    <img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp1.jpg"/>
                                </PropertyItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyItem listfor='For Sale'
                                    price="13000"category="Single Family Home"
                                    title="Luxury Family Home"
                                    >
                                        <img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp3.jpg"/>
                                </PropertyItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                            <PropertyItem listfor='For Rent'
                                    price="13000"category="Apartment"
                                    title="Luxury Family Home"
                                    featured
                                    >
                                        <img className="img-whp" src={data.fp4.childImageSharp.fluid.originalImg} alt="fp4.jpg"/>
                            </PropertyItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyItem listfor='For Sale'
                                featured
                                price="13000"
                                category="Appartment"
                                title="Renovated Apartment"
                                    >
                                    <img className="img-whp" src={data.fp15.childImageSharp.fluid.originalImg} alt="fp15.jpg"/>
                                </PropertyItem>
                            </div>
                            <div className="col-md-6 col-lg-6">
                                <PropertyItem listfor='For Rent'
                                price="13000"category="Villa"
                                title="Gorgeous Villa Bay View"
                                >
                                    <img className="img-whp" src={data.fp16.childImageSharp.fluid.originalImg} alt="fp16.jpg"/>
                                </PropertyItem>
                            </div>
                            
                            <div className="col-lg-12 mt20">
                                <Pagination/>
                            </div>                          
                        </div>
                    </div>
                </div>
            </div>
	    </section>
    )
}
export default V3Section